.review-edit-container {
  margin: auto;
  width: 750px;
  .review-edit-content {
    font-size: 14px;
    padding: 12px 20px 12px;
    background: white;
    box-shadow: 0px 0px 5px #efefef;
    margin: 25px;
    .publish-icon {
      display: flex;
      /* flex-direction: row-reverse; */
      font-weight: 700;
      color: green;
      font-size: 15px;
      justify-content: flex-end;
    }
    .product-name {
      padding-bottom: 8px !important;
    }
    .break-word {
      padding-bottom: 5px;
      .input-field {
        width: 97%;
        font-size: 13px;
        line-height: 17px;
        padding: 8px;
      }
    }
    // }
    .edit-btn {
      display: flex;
      justify-content: flex-end;
      padding-top: 8px;
      .btn {
        padding: 5px;
        font-size: 12px;
        width: 80px;
        margin-right: 18px;
      }
    }
  }
}
